.pay {
  background-color: #F5F5F5;
  border-radius: 2px;
  box-shadow: 0 0 2px 1px rgba(0,0,0,.15);
  color: #333;
  left: 0; 
  margin-left: auto; 
  margin-right: auto; 
  padding: 30px;
  position: fixed;
  right: 0;
  text-align: left;
  top: 20vh;
  width: 600px;
}

.pay-title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
}

.pay-background {
  background-color: rgba(0, 0, 0, 0.15);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.pay-checkout-button {
  background-color: #34B;
  border-radius: 3px;
  box-shadow: 0 4px 2px -2px rgba(0, 0, 0, 0.5);
  color: white;
  display: inline-block;
  margin-top: 16px;
  margin-left: 4px;
  margin-right: 4px;
  padding: 10px 20px;
  text-align: center;
}

.pay-checkout-button:hover {
  cursor: pointer;
}

.pay-loader {
  display: inline-block;
  width: 64px;
  height: 64px;
}
.pay-loader:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #fff;
  border-color: #fff transparent #fff transparent;
  -webkit-animation: pay-loader 1.2s linear infinite;
          animation: pay-loader 1.2s linear infinite;
}
@-webkit-keyframes pay-loader {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes pay-loader {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

